import React from 'react';
import TextField from 'material-ui/TextField';

export const TextInput = ({ value, label, onChange }) => (
    <TextField
        fullWidth
        value={value || ''}
        floatingLabelText={label}
        type="text"
        onChange={(event, change) => onChange(change)}
    />
);

export const NumberInput = () => (
    <div>NumberInput</div>
);

export const InputList = () => (
    <div>InputList</div>
);

export const Selection = () => (
    <div>Selection</div>
);

export const Suggestion = () => (
    <div>Suggestion</div>
);

export const SuggestionList = () => (
    <div>SuggestionList</div>
);

export const Toggle = () => (
    <div>Toggle</div>
);

export const Date = () => (
    <div>Date</div>
);

export const Period = () => (
    <div>Period</div>
);
