import React from 'react';
import { TextInput, NumberInput, InputList, Selection, Suggestion, SuggestionList, Toggle, Date, Period } from './Fields';

const componentMap = {
    'java.lang.Integer': NumberInput,
    'java.lang.String': TextInput,
    'org.hisp.dhis.validation.ValidationRuleGroup': InputList,
    'java.lang.Boolean': Toggle,
    'java.util.Date': Date,
    'java.util.Set': Selection,
    'java.util.List': SuggestionList,
    'org.hisp.dhis.period.Period': Period,
    'org.hisp.dhis.organisationunit.OrganisationUnit': Suggestion,
};

const Parameter = ({ parameter, handleParameterChange }) => {
    const Field = componentMap[parameter.klass];
    const label = parameter.fieldName;

    return <Field label={label} onChange={handleParameterChange} />;
};

export default Parameter;
